nav {
  background-color: #fafafa;
  /* position: fixed; */
  top: 0;
  margin-bottom: 10px;
  height: 50px;
  width: 100%;
  border-bottom: solid rgba(0, 0, 0, 0.07) 1px;
}

nav>li {
  display: inline-block;
  list-style: none;
  margin-top: 15px;
  margin-left: 15px;
  font-size: 20px;
}

#satellite {
  transform: scaleX(-1);
}

#satellite-div {
  margin-top: 63px;
  position: absolute;
  /* top: 180px; */
  /* left: 8%; */
  height: 10px;
  /* width: 500px; */
  width: 100%;
  /*background-color: pink;*/
  animation: rotate 2s linear infinite;
  /*box-shadowadow: 0 0 0 10px;*/
  /*border: solid black 1px;*/
  /*border-radius: 50%;*/
}

#earth {
  position: absolute;
  /* top: 100px; */
  /* left: 16%; */
  height: 300px;
  /* width: 300px; */
  width: 100%;
  display: flex;
  justify-content: center;
  border-radius: 50%;
  /*background-color: red;*/

}

#earth-img {
  border-radius: 50%;
  /*box-shadow: 0 0 0 10px blue;*/
}

#satellite-div::before {
  top: 0px;
  content: '';
  display: block;
  height: 0px;
  width: 60px;
  border-radius: 50%;
  /*background-color: black;*/
}

@keyframes rotate {
  0% {
    transform: rotateY(0deg);
  }

  20% {

    z-index: 2;
  }

  100% {
    transform: rotateY(360deg);
  }
}

.container{
  display: flex;
}

.satelliteContainer{
  flex:1;
  position: relative;
}

.my-info{
  margin-top: 50px;
  display: flex;
    flex-direction: column;
    flex: 0.8 1;
    align-items: center;
    justify-content: center;
}

.my-info > p{
  font-size: 1.3em;
} 
.social-media-container{
  margin-top: 130px;
  display: flex;
}
.social-media-container > a{
  text-decoration: none;
  color: black;
}
.social-media{
  display: flex;
  justify-content: center;
  align-items: center;
  border: solid #00000052 1px;
  border-radius: 2px;
  margin: 8px;
  flex:1;
}